#dtype{
  position:absolute;
  top:50%;
  left:50%;
  transform: translate(-50%, -50%);
  letter-spacing:5px;
  text-transform: uppercase;
  margin:0;
  border:0;
  padding:0;
  color: transparent;
}

#dtype:before{
  content: attr(data-text);
  position:absolute;
  top:0;
  height:100%;
  width:0%;
  border:0;
  color: #000;
  overflow: hidden;
  border-right:1px solid #000;
  -webkit-animation: type 15s linear infinite;
          animation: type 15s linear infinite;
  white-space: nowrap;
}

