@import url('https://fonts.googleapis.com/css2?family=Mulish&display=swap');


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


*{
  margin:0;
  padding:0;
  box-sizing:border-box;
  font-family: Mulish;
}

.navbar{
  background-color: #fff !important;
}



.navbar a {
    font-size:1.3rem;
    text-transform: capitalize;
}

.menu_active{
    font-weight: bold;
    border-bottom: 1px solid #565387;
}

.navbar:hover{
  color: #3498db !important;
}

 a:hover{
    color: #3498db !important;
}

a{
  text-decoration:none;
}

#header .btn-get-started{
  color:  #3498db !important;
  border: 1px solid #3498db;
  font-size:16px;
  font-weight:500;
  padding:10px 30px;
  border-radius: 45px;
  text-decoration:none;
  transition:1.5s;
  letter-spacing:1px;
  display:inline-block;

}

#header .btn-get-started:hover{
  background: #3498db;
  color: #fff !important;
}


#header h1{
  margin: 0 0 10px 0;
  font-size: 48px;
  font-weight: 700;
  color: black;
}

#header .brand-name{
  color: #3498db;
}

#header h2{
  color:#484848;
  font-size: 24px;
}

.header-img{
    animation: up-down 2s ease-in-out infinite alternate-reverse both;
}


@-webkit-keyframes up-down{
 0%{
    transform:translateY(10px);
 }
 100%{
    transform:translatey(-10px);
 }
}

@keyframes up-down{
  0%{
    transform:translateY(10px);
 }
 100%{
    transform:translatey(-10px);
 } 
}


*{
  margin:0;
  padding:0;
}

#dtype{
  position:absolute;
  top:50%;
  left:50%;
  transform: translate(-50%, -50%);
  letter-spacing:5px;
  text-transform: uppercase;
  margin:0;
  border:0;
  padding:0;
  color: transparent;
}

#dtype:before{
  content: attr(data-text);
  position:absolute;
  top:0;
  height:100%;
  width:0%;
  border:0;
  color: #000;
  overflow: hidden;
  border-right:1px solid #000;
  animation: type 15s linear infinite;
  white-space: nowrap;
}

